var that;
export default {
	data() {
		return {
			lodstatus:'',
			userlist: [],
			page: 1, 
			nolist: false
		};
	},
	methods: {
		getlist() {
			that.lodstatus = 'loading';
			that.nolist=false;
			that.$request
				.request({
					url: that.api.live.follow_list,
					method: 'post',
					data: { 
						page: that.page
					}
				})
				.then(res => {
					console.log(res)
					if (that.page == 1) {
						that.userlist = res;
						res.length==0?(that.lodstatus = '',that.nolist=true):(res.length>10?that.lodstatus = 'more':that.lodstatus = '')
					}else{
						if (res.length > 0) {
							that.userlist = that.userlist.concat(res);
							that.page++;
							that.lodstatus = 'more'; 
						} else {
							that.lodstatus = 'noMore';
							that.nolist=true 
						}
					} 
					uni.hideLoading();
				});
			
		},
		touser(e) { 
			var data = e.currentTarget.dataset;
			uni.navigateTo({
				url: './info/info?id='+ data.user_id
			});
		} 
	},
	onLoad(option) {
		that = this; 
		that.getlist();
	},
	onReachBottom() {
		that.getlist();
	}
};
